},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-12 col-lg-4 mb-4 mb-lg-0",attrs:{"data-aos":"fade-left"}},[_c('figure',[_c('img',{staticClass:"mb-4 mb-lg-0",attrs:{"src":require("@/assets/curso/temas/tema4/img25.png"),"alt":"Imagen Decorativa"}})])]),_c('div',{staticClass:"col-12 col-lg-8 mb-4 mb-lg-0"},[_c('p',[_vm._v("Son fases determinantes para conservar la calidad del fruto desde la finca hasta el consumidor final. En el empacado, los plátanos previamente seleccionados y clasificados son organizados en cajas de cartón corrugado, guacales plásticos o de madera, con separadores y protectores que evitan golpes y fricción entre los frutos. Este proceso se realiza bajo condiciones higiénicas, garantizando trazabilidad mediante etiquetas que incluyen información del productor, lote y fecha de corte, lo cual es clave para mercados internacionales. El transporte, por su parte, debe efectuarse en vehículos limpios y desinfectados, con una adecuada ventilación y, en el caso de exportaciones, bajo condiciones controladas de temperatura entre 14 y 16 °C. Durante el traslado se debe evitar la sobrecarga y la manipulación brusca que pueda generar magulladuras, rajaduras o deterioro en la piel del fruto, lo cual afectaría su vida útil y calidad comercial. Un adecuado empacado y transporte no solo reduce pérdidas poscosecha, sino que también fortalece la inocuidad, la competitividad y la aceptación del plátano en los mercados nacionales e internacionales.")])])])
